import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";

const Admin = () => {
  const [region, setRegion] = useState("경기");
  const [title, setTitle] = useState("");
  const [value, setValue] = useState(0);
  const [searchRegion, setSearchRegion] = useState("전국");
  const [searchData, setSearchData] = useState([]);

  useEffect(() => {
    const getData = async () => {
      await axios({
        method: "get",
        url: `${process.env.REACT_APP_API_URL}/admin/filter/getList?region=${searchRegion}`,
      })
        .then((res) => {
          setSearchData(res.data.filters);
          console.log(res.data.filters);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
  }, [searchRegion]);

  const handleSelect = (e) => {
    setRegion(e.target.value);
  };
  const onChangeTitle = (e) => {
    setTitle(e.target.value);
  };
  const onChangeValue = (e) => {
    setValue(e.target.value);
  };

  const handleSearchSelect = (e) => {
    setSearchRegion(e.target.value);
  };

  const countEvent = async (cal) => {
    if (window.confirm("연산하시겠습니까?")) {
      await axios({
        method: "post",
        url: `${process.env.REACT_APP_API_URL}/admin/filter/adjust`,
        data: {
          region: region,
          title: title,
          value: value,
          status: cal,
        },
      })
        .then(async (res) => {
          console.log(res.data);
          await axios({
            method: "get",
            url: `${process.env.REACT_APP_API_URL}/admin/filter/getList?region=${searchRegion}`,
          })
            .then((res) => {
              setSearchData(res.data.filters);
            })
            .catch((err) => {
              console.log(err);
            });
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      return;
    }
  };
  return (
    <Container>
      <FixedContainer>
        <CenteredText>여행대로 관리자 페이지</CenteredText>
        <InputContainer>
          <ContentArea>
            <ContentLabel>지역</ContentLabel>
            <RegionSelect onChange={handleSelect} value={region}>
              <option value="경기">경기</option>
              <option value="서울">서울</option>
              <option value="경북">경북</option>
              <option value="인천">인천</option>
              <option value="부산">부산</option>
              <option value="제주">제주</option>
              <option value="강원">강원</option>
              <option value="충남">충남</option>
              <option value="경남">경남</option>
              <option value="전남">전남</option>
              <option value="충북">충북</option>
              <option value="대전">대전</option>
              <option value="전북">전북</option>
              <option value="대구">대구</option>
              <option value="광주">광주</option>
              <option value="울산">울산</option>
              <option value="세종">세종</option>
            </RegionSelect>
          </ContentArea>
          <ContentArea>
            <ContentLabel>제목</ContentLabel>
            <TitleInput type="text" onChange={onChangeTitle} />
          </ContentArea>
          <ContentArea>
            <ContentLabel>숫자</ContentLabel>
            <CountInput type="number" onChange={onChangeValue} />
          </ContentArea>
          <ContentArea>
            <ContentLabel>연산</ContentLabel>
            <CountBtnArea>
              <CountBtn onClick={() => countEvent(0)}>적용</CountBtn>
              <CountBtn onClick={() => countEvent(1)}>곱하기</CountBtn>
              <CountBtn onClick={() => countEvent(2)}>나누기</CountBtn>
            </CountBtnArea>
          </ContentArea>
        </InputContainer>
      </FixedContainer>
      <SearchContainer>
        <RegionSelect
          onChange={handleSearchSelect}
          value={searchRegion}
          style={{ marginBottom: 20 }}
        >
          <option value="전국">전국</option>
          <option value="경기">경기</option>
          <option value="서울">서울</option>
          <option value="경북">경북</option>
          <option value="인천">인천</option>
          <option value="부산">부산</option>
          <option value="제주">제주</option>
          <option value="강원">강원</option>
          <option value="충남">충남</option>
          <option value="경남">경남</option>
          <option value="전남">전남</option>
          <option value="충북">충북</option>
          <option value="대전">대전</option>
          <option value="전북">전북</option>
          <option value="대구">대구</option>
          <option value="광주">광주</option>
          <option value="울산">울산</option>
          <option value="세종">세종</option>
        </RegionSelect>
        <SearchLabelArea>
          <SearchTitleLabel>제목</SearchTitleLabel>
          <SearchCountLabel>검색량</SearchCountLabel>
        </SearchLabelArea>
        {searchData.map((el, idx) => {
          return (
            <SearchDataArea key={idx}>
              <SearchTitle>{el.title}</SearchTitle>
              <SearchCount>{el.count}</SearchCount>
            </SearchDataArea>
          );
        })}
      </SearchContainer>
    </Container>
  );
};
const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  position: relative;
`;
const FixedContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 900px;
  justify-content: center;
  position: fixed;
  top: 0;
  padding-top: 30px;
  border-bottom: 1px solid #000;
  background-color: #fff;
`;
const CenteredText = styled.div`
  display: flex;
  width: 100%;
  font-size: 20px;
  font-weight: 800;
  justify-content: center;
  margin-bottom: 30px;
`;
const InputContainer = styled.div`
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
`;
const ContentArea = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 50px;
`;
const ContentLabel = styled.span`
  display: flex;
  font-size: 16px;
  font-weight: 700;
`;
const RegionSelect = styled.select`
  width: 120px;
  height: 24px;
`;
const TitleInput = styled.input`
  width: 300px;
  height: 24px;
`;
const CountInput = styled.input`
  width: 150px;
  height: 24px;
`;
const CountBtnArea = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 180px;
  height: 24px;
`;
const CountBtn = styled.button`
  width: 50px;
  height: 100%;
  background-color: #fff;
  border: 1px solid black;
  border-radius: 8px;
`;
const SearchContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 900px;
  height: 100%;
  margin-top: 171px;
`;
const SearchLabelArea = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
`;
const SearchTitleLabel = styled.div`
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 900;
  color: #000;
  text-align: center;
`;
const SearchCountLabel = styled.div`
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 900;
  color: #000;
  text-align: center;
`;
const SearchDataArea = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 5px 0px;
  border-bottom: 1px solid #999;
`;
const SearchTitle = styled.div`
  width: 50%;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  color: #000;
  text-align: center;
`;
const SearchCount = styled.div`
  width: 50%;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  color: #000;
  text-align: center;
`;

export default Admin;
